import { defineComponent } from "vue";
import Sidebar from "@/components/Sidebar.vue";
import CoinIcon from "@/components/CoinIcon.vue";
export default defineComponent({
    name: "App",
    components: { CoinIcon, Sidebar },
    computed: {
        benchmark() {
            const ethereum = this.$store.state.selectedCoins.find((coin) => {
                return coin.id === "ethereum";
            });
            return ethereum.multiple();
        },
    },
    methods: {
        getColor(coin) {
            if (!(this.benchmark || this.benchmark == 0))
                return "";
            const multiple = coin.multiple();
            if (coin.id === "ethereum")
                return " table-primary";
            else if (!multiple)
                return " table-secondary";
            else if (multiple >= this.benchmark * 1.5)
                return " table-success";
            else if (multiple < this.benchmark * 0.5)
                return " table-danger";
            return "";
        },
    },
});
